import axios from 'axios';

import { CONSTANTS } from './constants.js';

function initCoin (coinName, cb) {
  // This could return a promise instead of cb
  const coinBaseUrl = CONSTANTS.COINBASE_URL;
  function setCoin (response) {
    return {
      name: response.data.id, 
      fullname: response.data.display_name, 
      usdPrice: response.data.price_usd,
      eurPrice: response.data.price_eur, 
      diffOneDay: response.data.cap24hrChange,
      loaded: true
    };
  }
  return axios.get(coinBaseUrl.concat(coinName)).then(setCoin);
}

export const utils = {
  initCoin
}


