import React from 'react';

import { utils } from '../utils.js';

const textStyle = {
  color: 'white'
};

export default class Coin extends React.Component {
  constructor (props) {
    super();
    
    const io = require('socket.io-client');
    this.socket = io.connect('https://coincap.io');
    
    const coinName = props.match.params.coinName;
    this.coinName = coinName;
    
    this.state = {
      name: coinName, 
      fullname: '', 
      usdPrice: '',
      eurPrice: '', 
      diffOneDay: '',
      loaded: false
    }
  }
  // CREATE
  componentWillMount () {
    utils.initCoin(this.coinName).then(function (coinInfo) {
      this.setState(coinInfo);
    }.bind(this));
  }

  componentDidMount () {
    const instance = this;
		this.socket.on('trades', (data) => {
			const msg = data.message.msg;
			if (data.message.coin === this.coinName) {
				const coinInfo = {
					usdPrice: msg.price,
					diffOneDay: msg.cap24hrChange
				}
				instance.setState(coinInfo);
			}
		});
  }

  componentWillUnmount () {
    this.socket.disconnect();
  }

  render () {
    const coinInfo = this.state;
    return (
      <div>
        <ul style={textStyle}>
          <li>{coinInfo.name}</li>
          <li>{coinInfo.fullname}</li>
          <li>{coinInfo.usdPrice}</li>
          <li>{coinInfo.eurPrice}</li>
          <li>{coinInfo.diffOneDay}</li>
        </ul>
      </div>
    )
  }
}


